/* 0. Reset ================================= */
@import 'reset';


/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: white;
}


ul.products img{
	width: 200px;
	height: 200px;

} 

li{
	float: left;
	margin: 60px;
}

h1{
	margin-top: 5px;
	font-size: 20px;
	text-align: center;
}

h3{
	margin-top: 5px;
	font-size: 15px;
	text-align: center;
}

p{
	margin-top: 15px;
	font-size: 20px;
	text-align: center;
}

button{
    width: 100%;
    height: 25px;
    background: #FFDF3E;
}

/* 2. Content ================================= */

.content {
	position: relative;
	font-family: sans-serif;
	width: 90%;
	max-width: 500px;
	margin: 140px auto;
	padding: 30px 20px 30px 30px;
	.foreground {
		position: relative;
		z-index: 2;
		transform: translateZ(100px);

	
	}
	.background {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background: white;
		z-index: 1;
		// perspective: 2000px;
		transition: 0.2s;
		transform: translateX(20px) perspective(600px) rotateY(15deg);
	}
	&:hover .background {
		transform: translateX(-30px) perspective(600px) rotateY(-15deg);		
	}

	

	

	.logo {
		position: absolute;
		top: -30px;
		width: 60px;
		z-index: 2;
		transform: translateZ(100px);
		transition: 0.3s;

		&:hover {
			transform: rotate(-0.4turn)
		}
	}
	h1 {
		font-weight: 700;
		font-size: 22pt;
		margin: 15px 0;
	}
	p {
		color: #777;
		// margin: 10px 0;
		line-height: 140%;
	}
	p.github {
		font-size: 0.8em;
		
		a {
			opacity: 0.2;
			transition: 0.3s;
			&:hover {
				opacity: 1;
				color: red;
			}
		}
	}
	code {
		position: absolute;
		top: 20px;
		right: 20px;
		font-size: 0.8em;
		opacity: 0.2;
	}



}